
.DragAndDropArea {
  position: relative;
}
.DragAndDropAreaOverTop::before {
  background: #000;
  content: '';
  height: 2px;
  left: 0;
  pointer-events: none;
  position: absolute;
  top: -1px;
  width: 100%;
  z-index: 1;
}
.DragAndDropAreaOverBottom::after {
  background: #000;
  content: '';
  height: 2px;
  left: 0;
  pointer-events: none;
  position: absolute;
  bottom: -1px;
  width: 100%;
  z-index: 1;
}

.DragAndDropItemDragging {
  opacity: 0.3;
}


.DragAndDropHandle {
  cursor: move;
}
