.AdminPage {
  left: 180px;
  user-select: text;
}

.AdminWorldListContainerMinimize {
  font-size: 14px;
  opacity: 0.3;
}
.AdminActivity {
  padding-left: 50px;
  padding-bottom: 50px;
}
.AdminActivitySession {
  margin-bottom: 0px;
}
.AdminActivityUser, .AdminActivityEvening {
  margin-bottom: 20px;
}
.AdminActivityUser .AdminActivitySession, .AdminActivityEvening  .AdminActivitySession {
  margin-left: 20px;
}
.AdminActivitySessionTable {
  display: flex;
  flex-direction: row;
}
.AdminActivitySessionLog {
  margin-right: 1px;
  width: 5px;
  flex-grow: 0;
  flex-shrink: 0;
  border-top: 3px solid rgba(0, 0, 0, 0);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}
.AdminActivitySessionLogActivity {
  background: rgba(255, 255, 255, 0.1);
  height: 5px;
}
.AdminActivitySessionLog:hover {
  border-top-color: #fff;
}
.AdminActivitySessionLogRunning {
  background: rgba(255, 255, 255, 0.5);
}
.AdminActivitySessionLogUserActive {
  background: rgb(167, 127, 18);
}
.AdminActivitySessionLogPopulation {
  background: #437ce6;
}
.AdminActivitySessionLogMaxGen {
  background: #16ce63;
}

.AdminActivitySessionFeedback {
  background: rgb(22, 199, 22);
}
.AdminActivitySessionGameConstsChanged {
  background: red;
}
.AdminFeedbackMini {
  display: inline-block;
  border: 1px solid rgb(22, 199, 22);
  padding: 3px;
  margin: 3px;
  margin-left: 0px;
  font-size: 10px;
}

.AdminFeedback {
  margin: 20px;
}

.AdminTrainingStatsGroup {
  width: 200px;
  font-size: 12px;
}
.AdminTrainingStatsGroupSize {
  width: 20px;
}