
.WorldPage {
  flex: 1;
  display: flex;
  position: relative;
  width: 100%;
  overflow: hidden;
  pointer-events: none;
}
.WorldPage > * {
  pointer-events: visible;
}

.WorldPageLoading {
  flex: 1;
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  background: #000;
}

.WorldPage .Panel {
  overflow: hidden;
}

.WorldPage > .ErrorBoundary {
  position: relative;
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.Metrics {
  display: flex;
  flex-direction: column;
  min-width: 420px;
}

.WorldPagePixlingTemplates {
  min-width: 200px;
}


.EditBrainPopupLeft {
  width: 310px;
  display: block;
  align-self: stretch;
  padding: 20px;
  background: rgba(0, 0, 0, 0.2);
}

.LoadingFader {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  pointer-events: none;
}
.GameCountin {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 200px;
  font-weight: bold;
  pointer-events: none;
}
.CreateWorldFailed {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.MetricDefRowHidden {
  opacity: 0.5;
}
.MetricDefRowError {
  background: rgb(97, 23, 23);
}

.Histogram {
  display: flex;
  flex-direction: row;
  margin-bottom: 2px;
}
.HistogramBucket {
  width: 5px;
  height: 20px;
  background: rgb(17, 17, 34);
  display: flex;
  flex-direction: row;
  align-items: flex-end;
}
.HistogramValue {
  width: 100%;
  background: rgb(91, 91, 230);
}