.DerkUrlDropTarget {
  position: relative;
}
.DerkUrlDropTargetActive {
  position: absolute;
  left: 3px;
  top: 3px;
  bottom: 3px;
  right: 3px;
  background: rgba(255, 255, 0, 0.13);
  pointer-events: none;
  animation-duration: 3s;
  animation-name: dropTargetAnim;
  animation-iteration-count: infinite;
}
@keyframes dropTargetAnim {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}