.GameUI {
  position: absolute;
  left: 0px;
  top: 0px;
  bottom: 0px;
  right: 0px;
  pointer-events: none;
}
.HideUI .GameHUD > * {
  opacity: 0;
}

.GameControls {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.GameUI .Button {
  text-shadow: none;
}
.GameUI .Panel {
  text-shadow: none;
}
.GameUI, .GameUI input[type='text'], .GameUI input[type='number'] {
  text-shadow: 1px 1px rgba(0, 0, 0, 0.5);
}
.GameControlsRow {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.GameHUD {
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 0px;
  top: 0px;
  right: 0px;
  bottom: 0px;
  pointer-events: none;
}
.GameHUD > * {
  pointer-events: painted;
}
.GameHUDTop {
  margin: 10px;
  margin-bottom: 0px;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.GameHUDTopRight {
  align-items: center;
}
.FPSCounter {
  margin-right: 5px;
  position: relative;
  top: -4px;
  text-shadow: none;
}

.GameControls {
  opacity: 0;
  transition: opacity 0.1s;
}
.GameControls:hover {
  opacity: 1;
}


.GameHUDMiddle {
  display: flex;
  flex-direction: row;
  pointer-events: none;
  justify-content: space-between;
  flex-grow: 1;
  overflow: hidden;
  position: relative;
}
.GameHUDBottom {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin: 20px;
  margin-top: 10px;
  flex-shrink: 0;
}
.ArenaMinimapsContainer {
  margin: 10px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  position: absolute;
  right: 0px;
  top: 0px;
  bottom: 0px;
}

.GameHUDLeft, .GameHUDRight {
  display: flex;
  flex-direction: row;
  pointer-events: none;
  overflow: hidden;
}
.GameHUDLeft > *, .GameHUDRight > * {
  pointer-events: painted;
}
.GameHUDRight {
  flex-direction: row-reverse;
  flex-shrink: 0;
}
.GameHUDRow {
  display: flex;
  overflow: hidden;
  flex-direction: column;
  pointer-events: none;
}
.GameHUDRow > * {
  pointer-events: painted;
}

.TrainingHUD, .BattleHUD, .ReplayHUD {
  position: absolute;
  left: 50%;
  bottom: 20px;
  transform: translate(-50%, 0);
}
.TrainingHUD {
  pointer-events: none;
}
.TrainingHUD > * {
  pointer-events: visible;
}
.BattleHUD {
  width: 100%;
}
.BattleHUDTeams {
  width: 100%;
  justify-content: center;
  align-items: center;
}
.BattleHUDTeamsLeft, .BattleHUDTeamsRight {
  width: 40%;
}
.BattleHUDTeamsLeft {
  display: flex;
  justify-content: flex-end;
}

.RoundClock {
  width: 100px;
}
.TeamTrainingStats {
  padding: 2px;
}
.TeamTrainingStats:hover {
  background: rgba(0, 0, 0, 0.5);
}

.HideUI .ReplayHUD:hover {
  opacity: 1;
}

.TrainingWinTieLoss {
  width: 100px;
  height: 20px;
  display: flex;
  flex-direction: row;
}
.TrainingTies {
  background: rgb(143, 143, 143);
}

.TeamPanelNux {
  max-width: 300px;
}

.TeamActionBar {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.AwayTeam .TeamActionBar {
  flex-direction: row-reverse;
}
.TeamPanelHeader {
  font-size: 20px;
}
.HomeTeam .TeamPanelHeader {
  margin-right: 10px;
}
.AwayTeam .TeamPanelHeader {
  margin-left: 10px;
}
.TeamPanelTooMany {
  color: rgb(185, 46, 11);
  font-weight: bold;
}
.TeamPanel .ButtonIcon {
  width: 16px;
}


.BattleResultContainer {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  pointer-events: visible;
}
.BattleResult {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  max-height: 100vh;
  padding: 30px;
  padding-left: 50px;
  padding-right: 50px;
}
.BattleResultLoser .BattleResultHeader {
  font-size: 40px;
  font-weight: bold;
}
.BattleResultWinner .BattleResultHeader {
  font-size: 100px;
  font-weight: bold;
}

.ButtonFlat.PixlingsFilterTeamHome {
  color: rgb(94, 94, 255);
}
.ButtonFlat.PixlingsFilterTeamAway {
  color: red;
}

.WorldLog {
  width: 400px;
  height: 150px;
  position: absolute;
  left: 10px;
  top: 30px;
  overflow-x: hidden;
  overflow-y: hidden;
  font-size: 12px;
}
.WorldLog:hover {
  overflow-y: auto;
}
.WorldLogEntry {
  transition: opacity 0.5s;
  opacity: 1;
}
.WorldLogEntry:hover {
  background: rgba(255, 255, 255, 0.1);
}
.WorldLogEntryOld {
  opacity: 0;
}
.WorldLog:hover .WorldLogEntryOld {
  opacity: 0.8;
}

.BattleTopLeft {
  margin-left: 10px;
  width: 150px;
}
.BattleTopLeft h2 {
  margin: 0px;
}

.BrainHistogramKeys > .Row {
  justify-content: flex-end;
}
.BrainHistogramView {
  width: 512px;
}

.TrainingFAQ {
  width: 500px;
  height: 500px;
}

.NoBountiesWarning {
  width: 220px;
  text-align: center;
  pointer-events: none;
}
.NoBountiesWarning img {
  width: 70px;
}

.TeamName {
  justify-content: center;
  align-items: baseline;
  text-shadow: none;
}
