
.UnitsLayover {
  position: absolute;
  left: 0px;
  top: 0px;
}
.HideUI .UnitsLayover {
  opacity: 0;
}
.UnitLayover {
  position: absolute;
  left: 0px;
  top: 0px;
  transform: translate(-50%, -50%);
  pointer-events: visible;
}
.UnitLayoverClickarea {
  width: 100px;
  height: 100px;
  border: 2px solid transparent;
  cursor: pointer;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}
.UnitLayoverClickarea:hover {
  border-color: #fff;
}
.UnitLayoverClickarea .UnitLayoverGold {
  display: none;
}
.UnitLayoverClickarea:hover .UnitLayoverGold {
  display: block;
}
.UnitLayoverGold {
  font-weight: 900;
  color: rgb(240, 228, 123);
  font-family: 'Freckle Face', cursive;
  font-size: 23px;
  -webkit-text-stroke-width: 0.5px;
  -webkit-text-stroke-color: black;
}
.UnitLayoverGoldNegative {
  color: rgb(216, 52, 52);
  font-size: 23px;
}
.UnitLayoverGoldTicker {
  pointer-events: none;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  animation-duration: 0.7s;
  animation-delay: 0.3s;
  animation-name: GoldUpAnim;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  z-index: 3;
  font-size: 27px;
}
.UnitLayoverGoldTicker.UnitLayoverGoldNegative {
  z-index: 2;
  animation-name: GoldDownAnim;
}
@keyframes GoldUpAnim {
  0% {
    transform: translate(-50%, -50%);
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -100%);
    opacity: 0;
  }
}
@keyframes GoldDownAnim {
  0% {
    transform: translate(-50%, -50%);
    opacity: 1;
  }
  100% {
    transform: translate(-50%, 0%);
    opacity: 0;
  }
}