#PopupsContainer {
  position: fixed;
  left: 0px;
  top: 0px;
  right: 0px;
  bottom: 0px;
  pointer-events: none;
}
#PopupsContainer > * {
  pointer-events: visible;
}

.Fader {
  background: rgba(0, 0, 0, .5);
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  position: absolute;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.FaderPopupFullscreen {
  justify-content: stretch;
  align-items: stretch;
}
.Fader > .Popup {
  box-sizing: border-box;
  position: relative;
  flex-direction: column;
  overflow: hidden;
  max-height: 100%;
}
.FaderPopupFullscreen > .Popup {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.FaderPopupFullscreen > .WonkyPanel.Popup {
  margin: 30px;
}
.ClosePopup {
  position: absolute;
  right: -5px;
  top: -5px;
}
