.TestArena {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: stretch;
}
.TestArena > .Row {
  flex: 1;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.TestArena canvas {
  width: calc(100% - 600px);
}
.TestArena .ECSInspector {
  width: 600px;
}
.TestArena .ActionBar {
  position: absolute;
  left: 0px;
  bottom: 0px;
}
.TestArenaActions {
  position: absolute;
  left: 10px;
  top: 10px;
}