.Inventory {
  flex-shrink: 0;
}
.Inventory > .Button {
  margin-right: 10px;
}
.InventoryDrop {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
}
.ItemSlot {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.1);
  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.ItemSlotCount {
  position: absolute;
  top: -14px;
  left: 60%;
  transform: translate(-50%, 0);
}
.DraggableItem {
  cursor: move;
}
.UseableItem {
  line-height: 0px;
}
.ItemIcon {
  line-height: 0px;
}