

.Issues {
  flex: 1;
  overflow: hidden;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  margin-bottom: 10px;
  padding-bottom: 10px;
  user-select: text;
}

.IssuesSelection {
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  margin-bottom: 10px;
  padding-bottom: 10px;
}
.IssuesList {
  flex: 1;
}

.IssueListIssue .Popdown, .IssueListIssue .TooltipContainer {
  display: inline;
}
.IssueListIssueTopic {
  cursor: pointer;
}
.IssueListIssueTopic .CategoryIcon {
  width: 20px;
  text-align: center;
}
.CategoryIconSuggestion {
  color: rgb(95, 201, 121);
}
.CategoryIconBugReport {
  color: rgb(216, 108, 108);
}
.CategoryIconSupport {
  color: rgb(120, 120, 228);
}
.CategoryIconMisc {
  color: rgb(230, 230, 129);
}
.IssueListIssueClosed {
  opacity: 0.5;
}
.IssueListIssueVote {
  color: #888;
}
.IssueListIssueVote.ButtonToggled {
  color: yellow;
}

.NewIssue input, .NewIssue .CommentInput {
  margin-top: 10px;
  margin-bottom: 10px;
}

.IssuePage {
  overflow: hidden;
}

.IssuePageTitle {
  font-size: 20px;
}

.IssueTitleClosed {
  text-decoration: line-through;
}

.Comment {
  margin-bottom: 10px;
}
.NewComment {
  display: flex;
  flex-direction: row;
}
.NewComment .CommentInput {
  flex: 1;
}

.CommentInput {
  position: relative;
  min-height: 20px;
}
.CommentInputPlaceholder {
  position: absolute;
  left: 0px;
  top: 0px;
  color: #888;
}
.CommentInputContent {
  position: relative;
  flex: 1;
}

.CommentContent {
  white-space: pre-wrap;
}

.InlineIssueLink {
  background: rgba(255, 255, 255, 0.1);
  padding-left: 2px;
  padding-right: 2px;
}

@media screen and (max-width: 1420px) {
  .IssuesPanelMinimized {
    width: 30px;
  }
  .IssuesPanelMinimized .PanelTitle {
    display: none;
  }
  .IssuesPanelMinimized .IssuesPanelShowAllTitle {
    display: none;
  }
  .IssuesPanelMinimized .IssuesPanelNewTitle {
    display: none;
  }
  .IssuesPanelMinimized .NewIssueButtonCategoryTitle {
    display: none;
  }
}