@import '../../theme.css';

.MainMenuContainer {
  width: 100%;
  height: 100%;
  background: var(--main-background);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.MainMenu {
  pointer-events: all;
  position: relative;
}
.HideUI .MainMenu {
  opacity: 0;
}
.MainMenuPlaying {
  display: none;
}
.MainMenuPanel {
  width: 1100px;
  height: 652px;
}
.MainMenuPanel > .Row {
  flex: 1;
}
.MainMenuLogo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 150px;
  align-self: center;
  flex-shrink: 0;
  margin: 10px;
}
.MainMenuLogo img {
  width: 100px;
}
.MainMenuLeft {
  flex: 1;
  margin-right: 20px;
}
.MainMenuTraining {
  align-self: center;
}
.MainMenuTrainingTeams {
  justify-content: space-between;
}
.MainMenuTrainingTeam {
  align-items: center;
}
.MainMenuStartBattleContainer {
  margin: 10px;
  flex-shrink: 0;
}
.MainMenuStartBattle {
  font-weight: bold;
}
.MainMenuTrain {
  font-weight: bold;
}

.MainMenuBattle {
  flex: 1;
  overflow: hidden;
}
.MainMenuBattleBattleGroup {
  flex: 1;
  margin-top: 5px;
  margin-bottom: 5px;
}

.MainMenuBattleTeam {
  align-items: center;
}
.MainMenuBattleTeamTitle {
  align-items: center;
}


.TrainTeamSlot .PixlingPortrait {
  width: 60px;
  height: 60px;
}
.TrainTeamSlotDisabled {
  opacity: 0.2;
}




.MainMenuRight {
  width: 315px;
}
.MainMenuRight .BattleGroup {
  margin-bottom: 20px;
}

.TrainTeamSlot {
  line-height: 0px;
}

.TrainTeamSlotEmpty {
  border-radius: 100%;
  border: 5px dashed rgba(255, 255, 255, 0.3);
  width: 60px;
  height: 60px;
  box-sizing: border-box;
}
.MainMenuHeaderLogo {
  width: 50px;
}

.MainMenuTopRight {
  position: absolute;
  right: 10px;
  top: 10px;
}
.HideUI .MainMenuTopRight {
  opacity: 0;
}
.HideUI .MainMenuTopRight:hover {
  opacity: 1;
}

.MainMenuEnter {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.MainMenuEnter > img {
  margin-bottom: 40px;
}
.MainMenuEnter .Button {
  font-weight: bold;
}
