
.WorldCanvas {
  position: absolute;
  left: 0px;
  top: 0px;
  bottom: 0px;
  right: 0px;
  width: 100%;
  height: 100%;
  image-rendering: pixelated;
}
.WorldCanvasContent {
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 0px;
  top: 0px;
  bottom: 0px;
  right: 0px;
}

.ShaderErrors {
  position: absolute;
  right: 10px;
  top: 46px;
  color: red;
  background: #fff;
  padding: 6px;
}

.WorldCanvasErrors {
  position: absolute;
  left: 0px;
  top: 0px;
  bottom: 0px;
  right: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.WorldCanvasErrorsInner {
  overflow-y: auto;
  max-height: 100%;
  max-width: 500px;
}
.WorldCanvasContent {
  pointer-events: none;
}
