

.ArenaMinimaps {
  pointer-events: visible;
}
.ArenaMinimaps > .Column {
  padding-right: 10px;
  align-items: flex-end;
}

.ArenaMinimap, .AllArenasMinimap {
  position: relative;
  width: 50px;
  height: 50px;
  cursor: pointer;
  overflow: hidden;
}
.ArenaMinimapWinner, .ArenaMinimapTie, .ArenaMinimapLoss {
  position: absolute;
  top: -5px;
  right: -5px;
}

.ArenaMinimapUnit {
  width: 20px;
  height: 20px;
  position: absolute;
  /* transition: left 1s, top 1s; */
  transform: translate(-50%, -50%);
}
.ArenaMinimapUnit .PixlingPortrait {
  width: 20px;
  height: 20px;
}

.ArenaMinimapHpsContainer {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  display: flex;
  flex-direction: row;
  line-height: 0px;
}
.ArenaMinimapHps {
  width: 50%;
  display: flex;
  flex-direction: row;
  align-items: stretch;
}
.ArenaMinimapHpBar {
  flex: 1;
  display: flex;
}
.ArenaMinimapHp {
  height: 3px;
}

.AllArenasMinimap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.ArenaMinimapHomeGold, .ArenaMinimapAwayGold {
  font-family: 'Freckle Face', cursive;
  color: rgb(240, 228, 123);
}
