
.StructEditorProperty {
  display: flex;
  flex-direction: column;
}
.StructEditorProperty.Inline {
  flex-direction: row;
}
.StructEditorKey {
  color: #ded9b1;
  display: inline-block;
  line-height: normal;
  vertical-align: top;
}

.EnumStructEditor {
  display: flex;
  flex-direction: column;
}
.EnumStructEditorProperties {
  margin-left: 10px;
  padding-left: 3px;
}

[draggable="true"] > .EditorAction > .EditorActionButton {
  cursor: move;
}
.EditorActionButton {
  border: none;
  cursor: pointer;
  background: none;
}
.EditorActionButton i.material-icons {
  color: #999;
  display: block;
  font-size: 11px;
}

.LineListLayout {
  display: flex;
  flex-direction: column;
}
.LineListLayoutItem {
  align-items: flex-start;
  display: flex;
  flex-direction: row;
  flex-shrink: 0;
  position: relative;
}

.LineListLayoutItemVerticalLine {
  border-left: 1px solid rgba(255, 255, 255, 0.3);
  align-self: stretch;
}
.LineListLayoutItemVerticalLine.LineListLast {
  height: 13px;
  align-self: flex-start;
}
.LineListLayoutItemVerticalLine.LineListFirst {
  margin-top: 12px;
}
.LineListLayoutItemValue .LineListLayoutItemVerticalLine.LineListFirst {
  margin-top: 0;
}
.LineListLayoutItem:first-child > * > .LineListLayoutItemVerticalLine.LineListLast {
  height: 1px;
}
.LineListLayoutItemValue .LineListLayoutItem:first-child > * > .LineListLayoutItemVerticalLine.LineListLast {
  height: 13px;
}
.LineListLayoutItemLine {
  border-top: 1px solid rgba(255, 255, 255, .3);
  width: 5px;
  flex-shrink: 0;
  margin-top: 12px;
}
.LineListLayoutItemValue {
  padding: 2px;
}

.ListEditorItemAction {
  border: 1px solid rgba(255, 255, 255, .3);
  border-radius: 2px;
  flex-shrink: 0;
  height: 15px;
  margin-top: 5px;
  padding: 0;
  width: 15px;
}
.ListEditorItemAction:hover {
  border-color: #555;
}
.ListEditorItemAction:hover i.material-icons {
  color: #555;
}
.ListEditorItemAction.ListEditorItemSelected {
  background: rgba(255, 255, 255, 0.4);
}

.ShaderExpressionEditor {
  background: #363531;
  line-height: 26px;
}
div.ShaderExpressionEditor {
  align-items: flex-start;
  display: inline-flex;
  flex-direction: row;
  padding: 0 4px;
}
span.ShaderExpressionEditor {
  padding: 3px 0 4px;
}
.ShaderExpressionEditor .ShaderExpressionEditor {
  background: #3b4444;
}
.ShaderExpressionEditor .ShaderExpressionEditor .ShaderExpressionEditor {
  background: #3c3d34;
}
.ShaderExpressionEditor .ShaderExpressionEditor .ShaderExpressionEditor .ShaderExpressionEditor {
  background: #384238;
}
.ShaderExpressionEditor .ShaderExpressionEditor .ShaderExpressionEditor .ShaderExpressionEditor .ShaderExpressionEditor {
  background: #3f3f4b;
}
.ShaderExpressionEditor .ShaderExpressionEditor .ShaderExpressionEditor .ShaderExpressionEditor .ShaderExpressionEditor .ShaderExpressionEditor {
  background: #463743;
}
.ShaderExpressionEditor .ShaderExpressionEditor .ShaderExpressionEditor .ShaderExpressionEditor .ShaderExpressionEditor .ShaderExpressionEditor .ShaderExpressionEditor {
  background: #463c37;
}
.ShaderExpressionEditorBinaryOp {
  min-width: 16px;
  padding-left: 4px;
  text-align: center;
}
.ShaderExpressionEditorDropDown {
  margin-right: 3px;
}
.ShaderExpressionEditor .material-icons.ShaderExpressionEditorIcon {
  font-size: 16px;
  display: inline-flex;
  vertical-align: middle;
  margin-right: 5px;
}
.ShaderExpressionIndent {
  margin-left: 20px;
}
div.ShaderExpressionEditorVertical {
  flex-direction: column;
}

.WorldOpEditorRow {
  display: inline-block;
}
.WorldOpEditorType {
  background: #866282;
  margin-bottom: 2px;
  padding-left: 4px;
  display: inline-flex;
}
.WorldOpEditorType.DropDown {
  margin: 0;
  padding: 2px;
}
.WorldOpEditorComment {
  background: rgb(121, 121, 95);
  color: rgba(0, 0, 0, .25);
  padding: 2px 5px;
}
.WorldOpEditorExpand {
  color: #999;
  cursor: pointer;
  display: inline-block;
  margin-left: 5px;
  position: relative;
}
.WorldOpEditorExpandPreview {
  background: #fff;
  border: #666 solid 1px;
  box-sizing: border-box;
  cursor: default;
  overflow: auto;
  padding: 4px;
  position: absolute;
  z-index: 100;
}
.WorldOpEditorExpandPreview p {
  margin: 0;
}
.WorldOpEditorExpandPreview > * {
  pointer-events: none;
}
.WorldOpEditorRowBlock {
  background: rgba(0, 0, 0, 0.05);
  padding: 3px;
}
.WorldOpEditorRowBlockInside {
  background: #4c4f53;
  margin-top: 3px;
  padding-left: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.WorldOpSelectType {
  flex: 1;
}
.WorldOpDescription {
  opacity: 0;
}
.WorldOpSelectType:hover .WorldOpDescription {
  opacity: 1;
}

.ColorEditor {
  display: inline-block;
}
.ColorEditor .Button .WonkyPanel {
  margin: 0;
  text-shadow: #000 1px 1px 1px;
  min-width: 15px;
  min-height: 15px;
}

.MarkdownEditor {
  position: relative;
  display: flex;
  flex-direction: column;
}
.MarkdownEditorContent {
  flex: 1;
  overflow-y: auto;
}
.MarkdownEditorEditToggle {
  float: right;
  margin-left: 5px;
  margin-right: 5px;
}
.MarkdownEditor .CodeMirror {
  height: auto;
  margin-bottom: 5px;
}
.MarkdownEditor .Button {
  margin-right: 5px;
}
.MarkdownEditorEmpty {
  color: var(--dimmed-text-color);
  font-style: italic;
}

.TableEditor {
  border: 1px solid rgba(255, 255, 255, 0.1);
}
.TableEditor > tbody > tr > td, .TableEditor > tbody > tr > th {
  border-right: 1px solid rgba(255, 255, 255, 0.1);
}
.TableEditor > tbody > tr > th {
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.EnumEditor {
  cursor: pointer;
}
.EnumEditorVariant {
  display: flex;
  flex: 1;
}
.EnumEditorPopdown, .EnumEditorPopdown > .Menu {
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.EnumEditorPopdown > input {
  width: 100%;
}

input[type="number"].NumberEditorUncommitted, input[type="text"].StringEditorUncommited {
  border-bottom-style: dotted;
}
.StringEditorValidationError {
  background: red;
  color: #fff;
}
