.KeyframeEditorKey {
  width: 10px;
  height: 10px;
  background: white;
  margin: 2px;
}

.KeyframeEditorKeys {
  position: relative;
}
.CameraEditor .KeyframeEditorKeys {
  width: 700px;
}
.KeyframeEditorKeysBackground {
  position: absolute;
  left: 0px;
  top: 50%;
  height: 1px;
  right: 0px;
  background: rgba(255, 255, 255, 0.3);
}
.KeyframeEditorKey {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}
.KeyframeEditorKeySelected {
  background-color: yellow;
}
.KeyframeEditorTrackName {
  width: 150px;
}
.Playhead {
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 1px;
  background: yellow;
}