
.Row {
  display: flex;
  flex-direction: row;
}
.Column {
  display: flex;
  flex-direction: column;
}
.Column > .Row,
.Column > .Row > .Row,
.Column > .Row > .Row > .Row,
.Column > .Row > .Row > .Row > .Row,
.Column > .Row > .Column,
.Column > .Row > .Row > .Column,
.Column > .Row > .Row > .Row > .Column,
.Column > .Row > .Row > .Row > .Row > .Column {
  overflow-x: hidden;
}
.Block {
  display: block;
}
.Section {
  margin-top: 10px;
  margin-bottom: 10px;
}
.SectionHeading {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.5);
  border-bottom: 1px solid #444;
}
.Column.Scrollable {
  overflow-y: auto;
  padding-right: 5px;
}
.Row.Scrollable {
  overflow-x: auto;
  padding-bottom: 5px;
}
.Items.Row, .Items.Column {
  align-items: center;
}
.Items.Row > * {
  margin-left: 10px;
}
.Items.Row > *:first-child {
  margin-left: 0px;
}
.Items.Column > * {
  margin-top: 10px;
}
.Items.Column > *:first-child {
  margin-top: 0px;
}

.FloatList {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.FloatList > * {
  margin-right: 10px;
}

.Spreadsheet {
  border-collapse: collapse;
}
.Spreadsheet td, .Spreadsheet th {
  white-space: nowrap;
  border-right: 1px solid rgba(255, 255, 255, 0.3);
  padding-left: 4px;
  padding-right: 4px;
}
.Spreadsheet tr:hover {
  background: rgba(255, 255, 255, 0.05);
}
.Spreadsheet tr:first-child:hover {
  background: none;
}
.Spreadsheet td:last-child, .Spreadsheet th:last-child {
  border-right: 0px;
}

.FullscreenApp {
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 0px;
  top: 0px;
  bottom: 0px;
  right: 0px;
}
.FullscreenApp .Page {
  overflow-y: auto;
  position: absolute;
  left: 0px;
  right: 0px;
  top: 0px;
  bottom: 0px;
  pointer-events: visible;
}
.PageContent {
  width: 95vw;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 100px;
}

