
.Button {
  margin: 3px;
  padding: 0;
  background: none;
  border: none;
  color: #444444;
  font-size: 16px;
  cursor: pointer;
  text-shadow: none;
  text-decoration: none;
}
.Button:disabled, .Button:disabled small {
  color: #999;
}
.Button small {
  color: #444;
}
small .Button {
  font-size: inherit;
}

.Button .material-icons {
  font-size: 17px;
}
small .Button .material-icons {
  font-size: inherit;
}

.ButtonPrimary {
  font-size: 20px;
}

.ButtonFlat {
  color: #fff;
}
.ButtonFlat.ButtonToggled {
  color: #eee75a;
}
.ButtonLarge {
  font-weight: bold;
  font-size: 30px;
}
.ButtonLarge .material-icons {
  font-size: 30px;
}
