.ActionBar {
  text-shadow: none;
}
.ActionBarTitle {
  color: rgba(255, 255, 255, 0.5);
}
.Indicator {
  position: relative;
  width: 10px;
  height: 10px;
  background: #aaa;
  margin-right: 5px;
}
.IndicatorProgress {
  position: absolute;
  left: 0px;
  bottom: 0px;
  right: 0px;
  background: #fff;
}
.IndicatorContent {
  position: relative;
}
.IndicatorOverflow {
  outline: 3px solid red;
}
.IndicatorOn {
  background: rgb(112, 182, 93);
}
.IndicatorOff {
  background: rgb(190, 126, 88);
}
.IndicatorPressable {
  cursor: pointer;
}
.AbilityIndicator {
  width: 30px;
  height: 30px;
}
.FocusIndicators .Indicator {
  width: 30px;
  height: 30px;
}
.MovementIndicator {
  margin-right: 5px;
}
.SensesIndicator .Indicator {
  margin-bottom: 5px;
}

.ActionBar text {
  fill: rgba(255, 255, 255, 0.5);
}
line.IndicatorBackground, path.IndicatorBackground {
  stroke-width: 5px;
  stroke: #aaa;
}
line.IndicatorForeground, path.IndicatorForeground {
  stroke-width: 5px;
  stroke: #fff;
}
rect.IndicatorBackground {
  fill: #aaa;
}
rect.IndicatorForeground {
  fill: #fff;
}
.IndicatorCastingAbility {
  stroke: #ca3a15;
  stroke-width: 2px;
}