@import '../theme.css';

.SteamApp {
  user-select: none;
}

.AppHeaderRight {
  position: absolute;
  top: 5px;
  color: #ddd;
  text-shadow: 1px 1px rgba(0, 0, 0, 0.5);
  font-size: 16px;
}
.AppHeaderRight a {
  color: #b7ddff;
}
.AppHeaderRight {
  right: 15px;
}

.Center {
  text-align: center;
}
.RightAlign {
  text-align: right;
}

.LogoMedium {
  max-width: 400px;
  width: 100%;
  margin-bottom: 20px;
}
.LogoLarge {
  max-width: 500px;
  width: 100%;
  margin-bottom: 20px;
}

.PageCentered {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1;
}

.PixlingPage .PixlingPortrait {
  width: 400px;
  height: 400px;
}

.User.Page {
  padding-top: 30px;
}

.DownloadableSVG {
  position: relative;
}
.DownloadableSVGSave {
  position: absolute;
  bottom: 10px;
  right: 10px;
  opacity: 0;
  cursor: pointer;
}
.DownloadableSVGSave:hover {
  opacity: 1;
}

.YScroll {
  overflow-y: auto;
  padding: 10px 10px 0 0;
}

.LoggedInWidget {
  display: inline-block;
}

.FixWorldPage .CodeMirror {
  width: 800px;
  height: 700px;
}

.StarContainer {
  display: inline-flex;
}

.WorldListCard > h3 {
  min-height: 65px;
}

.Changelog {
  user-select: text;
}
.ChangelogEntry {
  margin-bottom: 20px;
}
.ChangelogVersion {
  /* border: 1px solid #fff; */
  margin-right: 5px;
  background: rgba(0, 0, 0, 0.5);
  padding: 3px 5px 3px 5px;
}
.ChangelogBody {
  font-size: 14px;
}
.ChangelogBody img {
  max-width: 100%;
}

.HomePageLogo {
  width: 500px;
  max-width: 100%;
  margin: 30px;
}

.Notifications {
  position: absolute;
  bottom: 50px;
  right: 30px;
}
.NewBuildNotif {
  text-align: center;
}

.BattleNotifContent .fa-hourglass-half {
  color: rgb(113, 113, 255);
}
.BattleNotifContent .fa-trophy {
  color: rgb(241, 241, 74);
}
.BattleNotifContent .fa-frown {
  color: rgb(194, 73, 43);
}
.BattleNotifContent .fa-times {
  color: rgb(163, 163, 163);
}
