.HomeAbout {
  width: 400px;
  text-align: center;
}
.HomeTagline {
  font-size: 20px;
}
.HomeTrailer {
  margin-top: 30px;
}

.WhatsNew {
  width: 500px;
  height: 500px;
}

.TestServerWarning {
  font-size: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
}
.TestServerWarning > div {
  background-color: yellow;
  color: black;
  padding: 10px;
}

.MyTeamsPanel {
  display: flex;
  flex-direction: column;
  justify-content: stretch;
}

.DemoTimer {
  position: absolute;
  left: 50%;
  top: 20px;
  transform: translate(-50%);
  width: 150px;
  text-transform: uppercase;
}
.DemoTimer > .Panel {
  max-width: 200px;
}

.MountRoukeLogo {
  margin-top: 100px;
  width: 90px;
}
.MountRoukeLogoSteam {
  width: 300px;
}

.SteamLoading {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex: 1;
}

.IssuesPanel {
  position: absolute;
  left: -15px;
  bottom: 20px;
  pointer-events: visible;
  width: 130px;
}
.HideUI .IssuesPanel {
  display: none;
}
.IssuesPanelShow {
  height: calc(100% - 70px);
  width: 350px;
}
