
.Tab {
  display: flex;
  flex: 1;
  flex-direction: column;
  overflow: hidden;
}
.TabsLeft, .TabsRight {
  display: flex;
  flex-direction: row;
}
.TabsTop {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  overflow: hidden;
}
.TabsBottom {
  display: flex;
  flex-direction: column-reverse;
}
.TabsLeft .TabsBar, .TabsRight .TabsBar {
  flex-direction: column;
}
.TabsBar {
  display: flex;
  flex-direction: row;
  flex-shrink: 0;
  flex-wrap: wrap;
}
.TabsBar > * {
  margin: 3px;
  margin-right: 15px;
}
.TabsBar > *:last-child {
  margin-right: 3px;
}
.TabsBar > * {
  border-bottom: 3px solid rgba(255, 255, 255, 0.3);
}
.TabsTop .TabsBar > * {
  margin-bottom: 5px;
}
.TabsLeft .TabsBar {
  align-items: flex-start;
}
.TabBody {
  display: flex;
  flex: 1;
  flex-direction: column;
  padding: 3px;
  overflow: hidden;
}

.AccordionTabTitle .Chevron {
  margin-left: auto;
  color: var(--accordion-item-chevron-color);
}

.Accordion .AccordionTabTitle {
  background: var(--accordion-item-background-color);
  padding: 5px;
  margin-bottom: 5px;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  user-select: none;
}
.Accordion .TabBody {
  padding: 5px;
  margin: 5px;
}
.AccordionGroupName {
  margin-top: 20px;
  margin-bottom: 5px;
  font-size: 12px;
  color: var(--accordion-item-title-text-color);
}
