.BountyEditor {
  max-width: 700px;
}
.BountyEditor .GoldCoin {
  width: 20px;
  margin-right: 4px;
}
.BountyNumberEditor {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.BountyIcons .fa-bomb {
  color: rgb(247, 91, 71);
}
.BountyIcons .fa-skull {
  color: rgb(129, 33, 16);
}
.BountyIcons .fa-heart {
  color: rgb(97, 245, 83);
}
.BountyIcons .fa-map-marker-alt {
  color: rgb(155, 187, 255);
}
.BountyIcons .fa-shield-alt {
  color: rgb(163, 163, 163);
}
.BountyIcons .fa-trophy {
  color: rgb(228, 217, 67);
}
.BountyIcons .fa-frown {
  color: rgb(201, 48, 28);
}
.BountyEditorPresets {
  align-items: center;
}
.BountyEditorPresets > div {
  display: flex;
  flex-direction: row;
  align-items: center;
}