.Menu {
  display: flex;
  flex-direction: column;
  background: rgb(26, 26, 26);
  padding: 2px;
  overflow-y: scroll;
  overflow-x: hidden;
}
.MenuItem {
  padding: 5px;
}
.MenuItemClickable {
  cursor: pointer;
}
.MenuItemClickable:hover {
  background: rgb(51, 51, 51);
}
.MenuItemDisabled {
  cursor: default;
  color: #888;
}
.MenuItemHighlighted {
  background: #444;
}

.ActionMenu.Popdown {
  display: inline-flex;
}
.ActionMenuAction {
  display: inline-flex;
  cursor: pointer;
}
