.Gym, .Gym canvas, .GymUI {
  position: absolute;
  left: 0px;
  top: 0px;
  bottom: 0px;
  right: 0px;
}
.Gym canvas {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.GymActionBar {
  margin: 10px;
}

.GymUI .ECSInspector {
  position: absolute;
  right: 0px;
  top: 0px;
  bottom: 0px;
  background: rgba(0, 0, 0, 0.8);
}
.GymUIBottom {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translate(-50%, 0);
}

.BattleTeamsHome, .BattleTeamsAway {
  min-width: 300px;
}
.BattleTeamsHome {
  text-align: right;
}