
.CreaturesBrowser {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow: hidden;
}
.CreaturesBrowserList {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.Creature {
  display: flex;
  flex-direction: row;
  width: 150px;
  text-align: center;
}
.Creature > .DerkUrlDropTarget > .Column {
  align-items: center;
}
.Creature .LivePixlingPortrait canvas, .Creature .PixlingPortrait {
  width: 150px;
  height: 150px;
}
.Creature .fas {
  color: rgb(236, 236, 85);
}
.Creature .far {
  color: rgb(255, 255, 255, 0.3);
}

.CreatureDragable {
  cursor: move;
}

.CreatureItems {
  margin-top: 5px;
  margin-bottom: 5px;
}

.CreaturesBrowserPages {
  display: flex;
  flex-direction: row;
  align-items: center;
}